<template>
  <v-container style="margin-top: 50px">
    <v-flex v-if="show_table" class="pa-5">
      <v-card>
        <v-toolbar
          flat
          color="indigo darken-1"
          dark
          max-width="100% !important"
          class="pa-0"
          :loading="loading_info"
        >
          <v-toolbar-title>
            <v-icon>mdi-analysis</v-icon>
            Productos
          </v-toolbar-title>
        </v-toolbar>

        <!-- TABLE -->
        <v-card-title>
          <v-dialog v-model="dialog" persistent max-width="600px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                v-bind="attrs"
                v-on="on"
              >
                Adicionar
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">Adicionar Producto</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        label="Nombre"
                        required
                        v-model="name"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        label="Precio"
                        v-model="price"
                      ></v-text-field>
                    </v-col>
             
                    <v-col cols="12">
                      <v-text-field
                        label="Descripcion"
                        v-model="description"
                        required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
                <small>Todos los campos son requeridos</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="dialog = false"
                >
                  Cerrar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="addProduct()"
                >
                  Adicionar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search_info"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="info_headers"
          :items="info"
          :items-per-page="50"
          class="elevation-1"
          :search="search_info"
          :loading="loading_info"
        >
        <!-- <template v-slot:item.price="{ item }">
          <v-chip
            color="purple lighten-3"
            dark
          >
            {{ item.price }}
          </v-chip>
        </template> -->
        </v-data-table>
      </v-card>
    </v-flex>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    dialog: false,
    search_info: "",
    info_headers: [
      { text: "Nombre", value: "name" },
      { text: "Precio", value: "price" },
      { text: "Descripcion", value: "description" }
    ],

    name: '',
    price: 0,
    description: '',
    loading_info: false,
    show_table: true,
    server: 'http://localhost:9292',
    info: [],
  }),

  mounted: function () {
    if (this.token == ''){
      this.$router.push('/');
    }else{
      this.getProducts();
    }
  },

  computed: {
    token: {
      get (){
        return this.$store.state.token
      }
    }
  },
  
  methods: {
    getProducts() {
      this.loading_info = true;

      axios({
        method: "GET",
        url: this.server + "/api/v1/users/" + this.token + "/products",
      }).then(
        (result) => {
          console.log(result.data)
          this.info = result.data.data;
          this.loading_info = false;
        },
        (error) => {
          console.log(`error ${error}`);
          this.loading_info = false;
        }
      );
    },
    addProduct(){
      axios({
        method: "POST",
        url: this.server + "/api/v1/products",
        data: { name: this.name, price: this.price, description: this.description, user_id: this.token }
      }).then(
        (response) => {
          this.dialog = false;

          if (response.data.status == 'success') {
            this.info.push({name: this.name, price: this.price, description: this.description, user_id: this.token})
          }else{
            console.log('fallo');
          }

          this.name = '';
          this.price = '';
          this.description = '';
        },
        (response) => {
          console.log(response);
          this.dialog = false
        }
      );
    }
  },
};
</script>
